*{margin:0;padding:0;box-sizing:border-box;font-family:"Play",sans-serif;color:#290c0c}.home{position:relative;width:100%;height:90%;display:flex;justify-content:center;align-items:center}@media screen and (min-width: 1024px){.home{max-width:1280px;margin:auto}}.home__background{position:absolute;top:0;left:0;width:100%;height:100%;background-image:url(/Portfolio/static/media/home-bg.955a3a20.jpg);background-position:center;background-size:cover;z-index:0}.home__greeting{z-index:1;display:flex;flex-direction:column;height:25vh;width:100%;max-width:480px;justify-content:space-around;align-items:center;background-color:rgba(0,0,0,.5);padding:1rem 2rem}.home__greeting--intro{text-align:center}.home__greeting--intro h1{color:#fefffa;font-size:1.5rem}.home__greeting--intro h2{color:#fefffa;font-size:1rem}.home__greeting--button{background-color:#1243ed;display:inline-block;color:#fefffa;font-weight:bold;padding:15px 25px;border:none;cursor:pointer;border-radius:30px;border:2px solid transparent;transition:.3s all ease-out;box-sizing:content-box}.home__greeting--button:hover{border:2px solid #fefffa;scale:1.3}.home__socials{position:absolute;left:calc(50% - 60px);bottom:8%;offset-position:left 60px;width:120px}.home__socials--icon{width:30px;margin:15px}
*{margin:0;padding:0;box-sizing:border-box;font-family:"Play",sans-serif;color:#290c0c}.projectcard{background-color:rgba(0,0,0,.5);height:100%;width:100%;display:flex;flex-direction:column;align-items:center;padding:10px 0;color:#fefffa;border-radius:10px}@media screen and (min-width: 768px){.projectcard{height:100%;width:100%}}.projectcard>*{margin-bottom:30px}@media screen and (min-width: 768px){.projectcard>*{margin-bottom:10px}}.projectcard__photo{height:40%;width:auto;border-radius:10px}.projectcard__name{color:#fefffa}.projectcard__description{color:#fefffa;font-weight:normal;text-align:center;font-size:.9rem}.projectcard__technologies{color:#fefffa;display:flex;flex-direction:column;align-items:center;font-size:1rem}.projectcard__technologies--label{color:#fefffa;font-size:.8rem}.projectcard__technologies--list{display:flex;flex-wrap:wrap;justify-content:center;align-items:center;width:100%}.projectcard__technologies--list-items{background-color:#fefffa;padding:5px;margin:5px 5px;border-radius:30px;font-size:.8rem}.projectcard__links{display:flex;justify-content:space-evenly;width:100%}.projectcard__links--item{color:#fefffa;text-decoration:none;display:flex;align-items:center}.projectcard__links--item .arrow{height:.8rem;fill:#fefffa}
